import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Instinct.css';

const talents = [
  { name: ' ', image: 'path/to/selfdefense.jpg', info: '  :  100 000    .' },
  { name: ' ', image: 'path/to/endurance.jpg', info: '  :  2 .' },
  { name: ' ', image: 'path/to/endurance.jpg', info: '   .' },
  { name: ' ', image: 'path/to/swimming.jpg', info: '    .' },
  //    
];

function Intellect() {
  const [selectedTalent, setSelectedTalent] = useState(null);

  const handleInfoClick = (talent) => {
    setSelectedTalent(talent);
  };

  const handleBackClick = () => {
    setSelectedTalent(null);
  };

  return (
    <div className="Instinct">
      <h1></h1>
      {selectedTalent ? (
        <div className="talent-info">
          <img src={selectedTalent.image} alt={selectedTalent.name} />
          <h2>{selectedTalent.name}</h2>
          <p>{selectedTalent.info}</p>
          <button onClick={handleBackClick}></button>
        </div>
      ) : (
        <div className="talents">
          {talents.map((talent) => (
            <div key={talent.name} className="talent">
              <img src={talent.image} alt={talent.name} />
              <h2>{talent.name}</h2>
              <button onClick={() => handleInfoClick(talent)}></button>
            </div>
          ))}
        </div>
      )}
      <Link to="/">
        <button className="menu-button"></button>
      </Link>
    </div>
  );
}

export default Intellect;
