import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Instinct.css';

const talents = [
  { name: ' ', image: '/images/radioustoichiv.jpg', info: '  :  3     ( .)' },
  { name: ' ', image: '/images/zachitsxozyina.jpg', info: '  :      2 ,     30%' },
  { name: '  ', image: '/images/nevkuseda.jpg', info: '  :  100  .' },
  { name: '', image: '/images/istreblenie.jpg', info: '  :  100   .' },
  { name: '', image: '/images/gurman.jpg', info: '  :  200 .  .' },
  { name: ' ', image: '/images/zapasvod.jpg', info: '  :     200 .' },
  { name: ' ', image: '/images/oxotmetka.jpg', info: '  :  5    .' },
  { name: ' ', image: '/images/silabalans.jpg', info: '  :     10 000 .' },
  { name: ' ', image: '/images/samozagivleniy.jpg', info: '  :     30 000 .   .' },
  { name: '', image: '/images/mestb.jpg', info: '  :    5 000 .  .' },
  { name: '', image: '/images/adrenalin.jpg', info: '  :  10         30%.' },
  { name: '', image: '/images/ohotnik.jpg', info: '  :  200   ( ).' },
  { name: '', image: '/images/rykzak.jpg', info: '  :   1         .' },
  //    
];

function Instinct() {
  const [selectedTalent, setSelectedTalent] = useState(null);

  const handleInfoClick = (talent) => {
    setSelectedTalent(talent);
  };

  const handleBackClick = () => {
    setSelectedTalent(null);
  };

  return (
    <div className="Instinct">
      <h1></h1>
      {selectedTalent ? (
        <div className="talent-info">
          <img src={selectedTalent.image} alt={selectedTalent.name} />
          <h2>{selectedTalent.name}</h2>
          <p>{selectedTalent.info}</p>
          <button onClick={handleBackClick}></button>
        </div>
      ) : (
        <div className="talents">
          {talents.map((talent) => (
            <div key={talent.name} className="talent">
              <img src={talent.image} alt={talent.name} />
              <h2>{talent.name}</h2>
              <button onClick={() => handleInfoClick(talent)}></button>
            </div>
          ))}
        </div>
      )}
      <Link to="/">
        <button className="menu-button"></button>
      </Link>
    </div>
  );
}

export default Instinct;

